//Component to display the info from the service call
import React from 'react';
import styles from './styles.module.css';
import MuiAccordion from '@mui/material/Accordion';
import MuiAccordionSummary, {
} from '@mui/material/AccordionSummary';
import MuiAccordionDetails from '@mui/material/AccordionDetails';

export const InfoHolder = (props) => {

    return (
    <MuiAccordion className = {styles["accordion__main"]}>
        <MuiAccordionSummary 
            expandIcon={<span className = {styles["accordion__expand"] /*opted to style a character over installing an icon library */}>⌄</span>}>
        <div className = {styles["accordion__title"]}>{props.info.title}</div>
        </MuiAccordionSummary>
        <MuiAccordionDetails>
        <div>
            {props.info.explanation}
        </div>
        </MuiAccordionDetails>
    </MuiAccordion>
    )
}
