//Component that is displayed on load and is used to align all other components
import React from 'react'
import styles from './styles.module.css';
import {DateField} from '../../components/DateField';
import {ImageHolder} from '../../components/ImageHolder';
import {InfoHolder} from '../../components/InfoHolder';
import {ApplicantDetailsHolder} from '../../components/ApplicantDetailsHolder';
import { AdapterMoment } from '@mui/x-date-pickers/AdapterMoment';
import {
    LocalizationProvider
  } from "@mui/x-date-pickers";
import { NASA_API_URI, API_KEY } from '../../constants';



const HomePage = () => {

    const [imageData, setImageData] = React.useState(null);
    const [loading, setLoading] = React.useState(false);

    const requestImage = (date) => {
        setLoading(true);
        fetch(NASA_API_URI + new URLSearchParams(
            {
                api_key: API_KEY,
                date: date
            }
        ))
        .then(results => results.json()) //Put json object into JS object
        .then(data =>
            {
            setImageData(data);
            setLoading(false);
            }
        );
    }

    return (
        <LocalizationProvider dateAdapter={AdapterMoment}>
        <div className = {styles["home__container"]}>
            <DateField  submit = {requestImage} imageLoading = {loading}/>
            {imageData && //I initially planned to have todays image load by default, but I ran out of time
            <ImageHolder imageUrl = {imageData.url} type = {imageData.media_type}>
                <InfoHolder info = {imageData} />
            </ImageHolder>
            }
            <ApplicantDetailsHolder />
        </div>
        </LocalizationProvider>
    )
}

export default HomePage;