//Component to display the date input section
import React from 'react';
import styles from './styles.module.css';
import { TextField, Card, CardContent, CardActions, Button, Typography} from '@mui/material';
import { DesktopDatePicker } from '@mui/x-date-pickers/DesktopDatePicker';
import moment from 'moment'
import {DATE_FORMAT, MIN_DATE} from '../../constants'

export const DateField = (props) => {
    const [selectedDate, setSelectedDate] = React.useState(null);
    const [errorMsg, setErrorMsg] = React.useState(null); //Also serves as a flag

    const handleChange = (newDate) => {
        setSelectedDate(newDate);
        //Leaving this in to show one of my failed logic attempts. The reason it doesn't work is that switch cases expect more strict types, such as strings.
        // switch(newDate)
        // {
        //     case moment(newDate).isValid(): {setErrorMsg("");}
        //     case !newDate: {setErrorMsg(""); break}
        //     case moment(newDate).isAfter(): {setErrorMsg("Date cannot be in the future"); break}
        //     case moment(newDate).isBefore("1995-06-16"): {setErrorMsg("Date cannot be before 06/16/1995"); break}
        //     default: break;
        // }

        //Check if date is valid
        if(moment(newDate).isValid() || !newDate){
            setErrorMsg(null)
        }
        else {
            setErrorMsg("Invalid date")
        }
        if(moment(newDate).isAfter()) {
            setErrorMsg("Date cannot be in the future");
        }
        else if(moment(newDate).isBefore(MIN_DATE)){
            setErrorMsg("Date cannot be before 06/16/1995");
        }
    };

    return (
        <Card className = {errorMsg ? styles["card__error"] : styles["card__container"]}> {/*I could use inline styles or a concat method for adding this second style, but I opted for this approach */}
            <CardContent className = {styles["card__date_container"]}>
                <Typography>
                    Input your desired date:
                </Typography>
                <div >
                    <DesktopDatePicker
                        onChange={(e) => handleChange(e)}
                        value={selectedDate}
                        disableFuture
                        minDate = {MIN_DATE}
                        renderInput={(item) => <TextField {...item} />}
                    />
                    <div className = {styles["text__error_message"]}>{errorMsg}</div>
                </div>
            </CardContent>
            <CardActions className={styles["card__button_container"]}>
                <Button
                className = {styles["button__main"]}
                variant="contained"
                disabled = {errorMsg || props.imageLoading || !selectedDate}
                onClick = {() =>props.submit(moment(selectedDate).format(DATE_FORMAT))}>
                    {props.imageLoading ? "...Loading" : "Submit"}
                </Button>
            </CardActions>
        </Card>
    )
}
