//Component to display the image section
import React from 'react';
import styles from './styles.module.css';
import { Card, CardContent} from '@mui/material'; 

export const ImageHolder = (props) => {

    return (
       <Card className = {styles["card__main"]}>
           <CardContent>
               {props.type == "image"?
               <img 
               className = {styles["card__image_main"]}
               src={props.imageUrl} alt = "A picture of some space" />
               :
               <a target="_blank" href={props.imageUrl}>Click here to watch the video!</a>
    }

           </CardContent>
            {props.children}
       </Card>

    )
}
